a { color: inherit; }

a:hover { color: #b1b1b0; text-decoration: none; }

header { position: relative; width: 100%; height: 0; background: none; }

header .head { height: 60px; display: flex; z-index: 999; justify-content: flex-end; align-items: flex-end; position: relative; border-bottom: 1px solid #ffffff; padding-right: 0; }

header .head .logout { z-index: 999; font-family: 'Roboto'; font-size: 18px; line-height: 30px; font-weight: 400; color: #ffffff; }

header .head .user-name { z-index: 999; font-family: 'Roboto'; font-size: 18px; line-height: 30px; font-weight: 400; color: #ffffff; margin-right: 30px; }

main #main { margin-top: -60px; background-repeat: no-repeat; background-position: center; background-size: cover; background-attachment: fixed; width: 100%; height: 108vh; position: relative; padding-top: 77px; }

main #main:before { content: ""; width: 100%; height: 100%; position: absolute; top: 0; left: 0; background: rgba(0, 0, 0, 0.6); }

main #main .my-container { display: flex; flex-wrap: wrap; padding: 100px 0 0 0; }

main #main .my-container .metro-big { margin: 0 10px 10px 0; position: relative; cursor: pointer; transition: all .4s ease; border: solid 2px transparent; }

main #main .my-container .metro-big:hover { border: solid 2px #ffffff; box-shadow: 0px 0px 6px 4px rgba(53, 89, 100, 0.5); }

main #main .my-container .metro-big:active { transform: scale(0.98, 0.98); }

main #main .my-container .metro-small { margin: 0 10px 10px 0; position: relative; cursor: pointer; transition: all .4s ease; border: solid 2px transparent; }

main #main .my-container .metro-small:hover { border: solid 2px #ffffff; box-shadow: 0px 0px 6px 4px rgba(53, 89, 100, 0.5); }

main #main .my-container .metro-small:active { transform: scale(0.98, 0.98); }

main #main .my-container .metro-huge { margin: 0 10px 10px 0; position: relative; cursor: pointer; transition: all .4s ease; border: solid 2px transparent; }

main #main .my-container .metro-huge:hover { border: solid 2px #ffffff; box-shadow: 0px 0px 6px 4px rgba(53, 89, 100, 0.5); }

main #main .my-container .metro-huge:active { transform: scale(0.98, 0.98); }

main #main .my-container .metro-huge { width: 200px; height: 200px; }

main #main .my-container .metro-huge p { font-size: 13px; color: #ffffff; text-align: center; font-family: 'Roboto'; }

main #main .my-container .metro-big { width: 200px; height: 95px; }

main #main .my-container .metro-small { width: 95px; height: 95px; }

main #main .my-container .space { margin-bottom: 10px; }

main #main .my-container .label { position: absolute; color: #ffffff; font-size: 16px; font-weight: 500; left: 10px; font-family: 'Roboto'; user-select: none; }

main #main .my-container .bottom { bottom: 5px; }

main #main .my-container .top { top: 5px; }

main #main .my-container .chat { background: #d61300; }

main #main .my-container .calendar { background: #00a9ec; }

main #main .my-container .store { background: #ff9000; }

main #main .my-container .people { background: #0e5d30; }

main #main .my-container .chrome { background: #ce4e4e; }

main #main .my-container .music { background: #5f5f5f; animation: flip 6s linear infinite; transform: rotateX(0deg); }

main #main .my-container .games { background: #34495e; }

main #main .my-container .twitter { background: #5ac6fa; }

main #main .my-container .fb { background: #3b5998; }

main #main .my-container .ps { background: #0c5fa1; }

main #main .my-container .bt { background: #2546e9; }

main #main .my-container .cc { background: #d61300; }

main #main .my-container .wmp { background: #f68935; }

main #main .my-container .tube { background: #c8312b; }

main #main .my-container .skype { background: #19bfe5; }

footer .footer { width: 100%; padding-top: 30px; padding-bottom: 30px; background: #00838f; border-top: 1px solid #ffffff; position: relative; z-index: 99999; border-bottom: 1px solid #ffffff; }

footer .footer .footer-ul { display: flex; flex-direction: column; }

footer .footer .footer-ul li { font-family: 'Roboto'; font-size: 18px; line-height: 20px; font-weight: 500; color: #ffffff; margin-top: 5px; }

footer .copyright { width: 100%; height: 40px; background: #3a3a3a; z-index: 99999; display: flex; justify-content: center; align-items: center; position: relative; }

footer .copyright span { font-family: 'Roboto'; font-size: 18px; line-height: 20px; font-weight: 500; color: #ffffff; }
